.element{
    border: 1px solid #ffffff;
}

.content {
    display: block;
    color: inherit;
    padding: 5px 10px;
}

.element:hover{
    border: 1px solid #000000;
}


.trElement{
    display: flex;
    flex-direction: column;
    align-items: end;
    position: relative;
}

.linkElement{
    color: #000000;
}

.linkElement:hover{
    color: #a59e9e;
}
